import React, { Component } from 'react';
import './options.css';

const options = {
    sixtyFour: ['MARIO', 'DONKEY KONG', 'LINK', 'SAMUS', 'DARK SAMUS', 'YOSHI', 'KIRBY', 'FOX', 'PIKACHU',
        'LUIGI', 'NESS', 'CAPTAIN FALCON', 'JIGGLYPUFF'],
    melee: ['PEACH', 'DAISY', 'BOWSER', 'ICE CLIMBERS', 'SHEIK',
        'ZELDA', 'DR. MARIO', 'PICHU', 'FALCO', 'MARTH', 'LUCINA', 'YOUNG LINK', 'GANONDORF', 'MEWTWO', 'ROY',
        'CHROM', 'MR. GAME & WATCH'],
    brawl: ['META KNIGHT', 'PIT', 'DARK PIT', 'ZERO SUIT SAMUS', 'WARIO', 'SNAKE',
        'IKE', 'POKÉMON TRAINER', 'DIDDY KONG', 'LUCAS', 'SONIC', 'KING DEDEDE', 'OLIMAR', 'LUCARIO', 'R.O.B.',
        'TOON LINK', 'WOLF'],
    four: ['VILLAGER', 'MEGA MAN', 'Wii Fit TRAINER', 'ROSALINA & LUMA', 'LITTLE MAC', 'GRENINJA',
        'PALUTENA', 'PAC-MAN', 'ROBIN', 'SHULK', 'BOWSER JR.', 'DUCK HUNT', 'RYU', 'KEN', 'CLOUD', 'CORRIN', 'BAYONETTA'],
    ultimate: ['INKLING', 'RIDLEY', 'SIMON', 'RICHTER', 'KING K. ROOL', 'ISABELLE', 'INCINEROAR', 'PIRANHAPLANT', 'JOKER'],
    mii: ['Mii Brawler', 'Mii Swordfighter', 'Mii Gunner'],
    echo: ['DARK SAMUS', 'DAISY', 'LUCINA', 'CHROM', 'KEN', 'RICHTER']
};

export default class Options extends Component {
    render() {
        return (
            <div className="option-container">
                <label>64:
                    <input type="checkbox"
                        defaultChecked={true}
                        onChange={e => this.props.toggleOption(e.target.checked, options.sixtyFour)}></input>
                </label>
                <label>Melee:
                    <input type="checkbox"
                        defaultChecked={true}
                        onChange={e => this.props.toggleOption(e.target.checked, options.melee)}></input>
                </label>
                <label>Brawl:
                    <input type="checkbox"
                        defaultChecked={true}
                        onChange={e => this.props.toggleOption(e.target.checked, options.brawl)}></input>
                </label>
                <label>Sm4sh:
                    <input type="checkbox"
                        defaultChecked={true}
                        onChange={e => this.props.toggleOption(e.target.checked, options.four)}></input>
                </label>
                <label>Ultimate:
                    <input type="checkbox"
                        defaultChecked={true}
                        onChange={e => this.props.toggleOption(e.target.checked, options.ultimate)}></input>
                </label>
                <label>Echo:
                    <input type="checkbox"
                        defaultChecked={true}
                        onChange={e => this.props.toggleOption(e.target.checked, options.echo)}></input>
                </label>
                <label>Mii:
                    <input type="checkbox"
                        defaultChecked={false}
                        onChange={e => this.props.toggleOption(e.target.checked, options.mii)}></input>
                </label>
            </div>
        );
    }
}