import React, { Component } from 'react';
import './sidebar.css';

export default class Sidebar extends Component {
    getIconPath(name) {
        return name.toLowerCase().replace(/^(.)|(?:\.\s)(.)|[\s.-](.)/g, letter => letter.toUpperCase()).replace(/[ &.]/g, '');
    }

    renderSidebarCharacters(i) {
        return (
            <div className="sidebar-character">
                <img src={`./images/${this.getIconPath(this.props.characters[i])}.png`} alt={this.props.characters[i]} height="156px"></img>
                <div className="name-block">
                    <span>{this.props.characters[i]}</span>
                </div>
            </div>)
    }

    render() {
        return (
            <div className="sidebar-container">
                {this.renderSidebarCharacters(0)}
                {this.renderSidebarCharacters(1)}
                {this.renderSidebarCharacters(2)}
                {this.renderSidebarCharacters(3)}
                {this.renderSidebarCharacters(4)}
            </div>
        );
    }
}