import React, { Component } from 'react';
import './character.css';

export default class Character extends Component {
    constructor(props) {
        super();
        this.iconPath = props.characterName.toLowerCase().replace(/^(.)|(?:\.\s)(.)|[\s.-](.)/g, letter => letter.toUpperCase()).replace(/[ &.]/g, '');
        this.onKeyDown = this.onKeyDown.bind(this);
    }

    onKeyDown(e) {
        if (e.key === 'Enter') {
            this.props.onClick(e);
        } else if (e.key.startsWith('Arrow')) {
            this.props.focus(e, this.props.characterName);
        }
    }

    render() {
        return (
            <div className={'container' + (this.props.selected ? ' selected' : '') + (this.props.banned ? ' banned' : '')}
                onClick={e => this.props.onClick(e)}
                onKeyDown={this.onKeyDown}
                tabIndex="0">
                {this.props.banned ? 
                <div className="banned-foreground">
                    <div className="left banned-block"></div>
                    <div className="right banned-block"></div>
                </div>
                : ''}
                <img src={`./images/${this.iconPath}.png`} alt={this.props.characterName} height="65px"></img>
                <div className="name-block">
                    <span>{this.props.characterName}</span>
                </div>
            </div>
        );
    }
}